import {AnimatePresence, motion, MotionProps, Reorder, useDragControls, useMotionValue} from "framer-motion";
import {Key, ReactElement, ReactNode, useState} from "react";
import ReorderIcon from "./ReorderIcon";
import {useRaisedShadow} from "../hooks/userRaisedShadow";

export interface DragItemProps<V>
{
    value: V,
    children: ReactNode,
    onClick: () => void
}

export default function DragItem<V>({value, children, onClick}: DragItemProps<V>) : ReactElement
{
    const controls = useDragControls();
    const y = useMotionValue(0);
    const boxShadow = useRaisedShadow(y);
    const [hover, setHover] = useState(false);

    return <Reorder.Item style={{boxShadow, y}} value={value} dragListener={false} dragControls={controls} className="my-2">
        <div className="d-flex rounded-1 p-1" style={{backgroundColor: "var(--light)"}}>
            <motion.div className="justify-content-center d-flex flex-column mx-2 w-100 position-relative" onHoverStart={() => setHover(true)}
                        onHoverEnd={() => setHover(false)} style={{cursor: "pointer"}} onTap={onClick}>
                {children}
                <AnimatePresence>
                    {hover && <motion.div className="position-absolute w-100 bg-danger text-light rounded-1 justify-content-center d-flex"
                                          initial={{opacity: 0}} animate={{opacity: 100}} transition={{duration: .15}} exit={{opacity: 0}}>
                        <i className="bi bi-trash fs-3 mx-auto"></i>
                    </motion.div>}
                </AnimatePresence>
            </motion.div>
            <ReorderIcon dragControls={controls} className="ms-auto mb-0 align-self-center"/>
        </div>
    </Reorder.Item>;
}