import {ReactElement} from "react";
import {Outlet} from "react-router-dom";
import Home from "./Home";

export default function Layout() : ReactElement
{
    return <>
        <header className="m-3 position-relative mb-5">
            <div className="d-flex justify-content-center">
                <img src="crane.svg" alt="logo" height="140" width="auto"/>
            </div>
            <h4 className="position-absolute text-uppercase text-light fw-bold end-0 top-0"
                  style={{letterSpacing: "1rem", writingMode: "vertical-rl"}}>crane</h4>
        </header>
        <Home/>
        <Outlet/>
    </>;
}