import {Children, DetailedHTMLProps, ReactElement, ReactNode} from "react";
import Button, {ButtonProps} from "./Button";
import {Link} from "react-router-dom";

export interface AddButtonProps  extends ButtonProps
{
}

export default function AddButton({onClick, ...props}: AddButtonProps) : ReactElement
{
    return <Button className="btn btn-dark border" onClick={onClick}>
        <span className="d-flex justify-content-center align-items-center">
            <i className="bi bi-plus-circle fs-3"></i>
            <span className="ms-3">Add</span>
        </span>
    </Button>;
}