import {ReactElement, ReactNode, useEffect, useState} from "react";

export interface BookmarkletButtonProps
{
    items: number[]
    children?: ReactNode
}

export default function BookmarkletButton({items, children}: BookmarkletButtonProps) : ReactElement
{
    const [link, setLink] = useState("");

    useEffect(() => {
        const href = `javascript:void%20function(){const%20a=[${items.join(',')}],b=document.querySelectorAll(%22input[placeholder=\\%22CRN%20Giriniz.\\%22]%22);for(let%20c=0;c%3Ca.length%26%26c%3Cb.length;c++){b[c].value=a[c],b[c].dispatchEvent(new%20Event("input"));}}();`;
        setLink(href);
    },[items]);

    return <a className="btn btn-dark border" href={link}>
        <span className="d-flex justify-content-center align-items-center">
            <i className="bi bi-box-arrow-up-right fs-3"></i>
            <span className="ms-3">Bookmarklet</span>
        </span>
    </a>;
}