import {ReactElement, useState} from "react";
import DragItem from "./DragItem";
import {useCourse} from "../CourseAPI";
import Spinner from "./Spinner";
import { motion } from "framer-motion";
import {AxiosError} from "axios";

export interface CoursePillProps
{
    crn: number,
    onClick: () => void
}

export default function CousePill({crn, onClick} : CoursePillProps) : ReactElement
{
    const {data: course, isLoading, isValidating, error} = useCourse(crn);

    return <motion.div initial={{x: "-25%", opacity: 0}} animate={{x: 0, opacity: 1}} exit={{x: "25%", opacity: 0}}
        transition={{duration: 0.2}}>
        <DragItem value={crn} onClick={onClick}>
            <div className="d-flex flex-row py-3 position-relative">
                <span className="fw-bold border-end border-dark px-1 border-2">{crn}</span>
                <span className="mx-3">{course?.courseTitle}</span>
                {isLoading && !error && <Spinner/> }
                {!!error && <span className="text-danger">
                    {error.message}
                </span>}
            </div>
        </DragItem>
    </motion.div>;
}