import useSWR, {SWRResponse} from "swr";
import axios from "axios";
import useSWRInfinite, {SWRInfiniteResponse} from 'swr/infinite'

const Base= "https://courseapi.cenngo.tech/";

const Endpoints = {
    SingleCourse: (crn: number) => `courses/${crn}`,
    Courses: (facultyCode: string, courseCode?: string, cursor = 0, limit = 10) =>
        `courses/${facultyCode}${!!courseCode ? ("/" + courseCode) : ""}?cursor=${cursor}&limit=${limit}`,
    MajorCodes: () => "courses/codes",
    CourseCodes: (facultyCode: string) => `courses/codes/${facultyCode}`,
    LastUpdate: () => "info/lastUpdate",
    EntryCount: () => "info/entryCount"
}

export interface Building{
    code: string,
    name: string
}

export interface CourseTime{
    dayOfWeek: DayOfWeek,
    startTime: Date,
    endTime: Date
}

export enum DayOfWeek{
    Sunday,
    Monday,
    Tuesday,
    Wednesday,
    Thursday,
    Friday,
    Saturday
}

export interface Course{
    crn: number,
    courseCode: string,
    facultyCode: string,
    courseTitle: string,
    teachingMethod: string,
    instructors: string[],
    buildings: Building[],
    times: CourseTime[],
    rooms: string[],
    capacity: number,
    enrolled: number,
    majorReservations: string,
    majorRestrictions: string[]
    prerequisites: string[][],
    classRestriction: ClassRestriction
}

export enum ClassRestriction{
    None= 1 << 0,
    YearOne= 1 << 1,
    YearTwo= 1 << 2,
    YearThree= 1 << 3,
    YearFour= 1 << 4
}

export interface SwrResult<Type>{
    isLoading: boolean,
    isValidating: boolean,
    error: any,
    data: Type | undefined
}

export interface SwrInfiniteResult<Type>{
    isLoading: boolean,
    isValidating: boolean,
    error: any,
    data: Type[] | undefined,
    size: number,
    setSize: (size: number | ((_size: number) => number)) => Promise<Type[] | undefined>
}

class AxiosError extends Error{
    status: number;

    constructor(status:number, message?: string) {
        super(message);
        this.status = status;
    }

}

async function fetcher<Type>(url: string)
{
    const res = await axios.get<Type>(url, {
        baseURL: Base
    });

    if (res.status !== 200)
        throw new AxiosError(res.status, res.status === 204 ? 'Not found.' : 'An error occurred while fetching the data.');

    return res.data;
}

function createResult<Type>(response: SWRResponse<Type, any>) : SwrResult<Type>
{
    const {isValidating, error, data} = response;

    return {
        isLoading: !error && !data,
        isValidating,
        data,
        error
    };
}

function createInfiniteResult<Type>(response: SWRInfiniteResponse<Type, any>) : SwrInfiniteResult<Type>
{
    const {isValidating, error, data, setSize, size} = response;

    return {
        isLoading: !error && !data,
        isValidating,
        data,
        error,
        size,
        setSize
    };
}

export function useCourse(crn: number) : SwrResult<Course>
{
    const url = Endpoints.SingleCourse(crn);
    const response = useSWR(!!crn ? url : null, fetcher<Course>);
    return createResult(response);
}

export function useCourses(facultyCode: string | null, courseCode?: string, cursor = 0, limit = 10) : SwrResult<Course[]>
{
    const url = !!facultyCode ? Endpoints.Courses(facultyCode, courseCode, cursor, limit) : null;
    const response = useSWR(url, fetcher<Course[]>);
    return createResult(response);
}

export function useCoursesInfinite(facultyCode: string | null, courseCode?: string) : SwrInfiniteResult<Course[]>
{
    function getKey(pageIndex: number, previousPageData: any) : string | null
    {
        if(!facultyCode)
            return null;

        if(!!previousPageData && "length" in previousPageData && previousPageData.length === 0)
            return null;

        if(pageIndex === 0)
            return Endpoints.Courses(facultyCode, courseCode, 0, 10);

        return Endpoints.Courses(facultyCode, courseCode, pageIndex * 10, 10);
    }
    const response = useSWRInfinite(getKey, fetcher<Course[]>);
    return createInfiniteResult(response);
}

export function useMajorCodes()
{
    const url = Endpoints.MajorCodes();
    const response = useSWR(url, fetcher<string[]>);
    return createResult(response);
}

export function useCourseCodes(facultyCode: string | null)
{
    const url = !!facultyCode ? Endpoints.CourseCodes(facultyCode) : null;
    const response = useSWR(url, fetcher<string[]>);
    return createResult(response);
}

export function useLastUpdate()
{
    const url = Endpoints.LastUpdate();
    const response = useSWR(url, fetcher<Date>);
    return createResult(response);
}

export function useEntryCount()
{
    const url = Endpoints.EntryCount();
    const response = useSWR(url, fetcher<number>);
    return createResult(response);
}